@-webkit-keyframes modal {
  from {
    opacity: 0;
    -webkit-transform:translate(0,-60px );
            transform:translate(0,-60px );
  }
  to{
    opacity: 1;
    -webkit-transform:translate(0,0);
            transform:translate(0,0);
  }
}
@keyframes modal {
  from {
    opacity: 0;
    -webkit-transform:translate(0,-60px );
            transform:translate(0,-60px );
  }
  to{
    opacity: 1;
    -webkit-transform:translate(0,0);
            transform:translate(0,0);
